@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,800&display=swap);
body {
  margin: 0;
  background: rgb(255,255,255);
  background: radial-gradient(circle, rgba(255,255,255,1) 45%, rgba(177,177,177,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff",endColorstr="#b1b1b1",GradientType=1);
  font-family: 'Open Sans', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bg-dark {
  background-color: transparent !important;
}

.navbar-light .navbar-nav .nav-link {
  color: rgba(0,0,0,.5);
  font-size: 0.6em;
  border-right: 1px solid #ccc;
  -webkit-transition: all 1s;
  transition: all 1s;
  text-transform: uppercase;
}

.navbar-light .navbar-nav .nav-link:hover {
  background: #006380;
  color: #ffffff !important;
}

.form-control {
  font-size: 0.7em !important;
  background-color: rgba(255,255,255,0.7) !important;
}

.btn {
  font-size: 0.7em !important;
}
.Footer {
  width: 100%;
  text-align: center;
}

.Footer h2 {
  font-weight: 100;
  font-size: 0.7em;
  padding: 10px;
}
.carousel {
  position: fixed !important;
  width: 100%;
  z-index: -1;
  top: 0;
}
.Site {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.Site-content {
  flex: 1 1;
}

.form-control {
  border-radius: 0 !important;
}

.btn {
  border-radius: 0 !important;
}
